/* ===== SECTION LAYOUT ===== */
#people {
    margin: 0 auto;
    min-height: 60vh;
    width: 100vw;
    max-width: 75rem;

    display: flex;
    flex-direction: row;
    align-items: stretch;

    padding: 0 1rem;
    margin-bottom: 4rem;
}

/* ===== LEFT (TEXT) ===== */
.peopleLeft {
    flex: 0 0 70%;
    display: flex;
    justify-content: center; /* already there */
    align-items: center;     /* vertically center if needed */
}

.peopleBody {
    width: auto;             /* remove 100% so it can shrink to fit content */
    max-width: 44rem;
    padding: 1rem;
    text-align: left;        /* keep text left-aligned inside the box */
}

.peopleTitle {
    text-align: center;
    font-size: 3rem;
    margin-bottom: 2rem;
    font-weight: 600;
}

.peopleDesc {
    font-weight: 300;
    font-size: 1rem;
    margin-bottom: 1.5rem;
    max-width: 35rem;
}

/* ===== LISTS ===== */
.peopleLists {
    margin-top: 1rem;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
}

.leftList {
    width: 16rem;
}
.rightList {
    width: 24rem;
}

.listHead {
    margin-bottom: 0.7rem;
    padding-left: 0.5rem;
    text-decoration: underline;
}

.bullet {
    margin-bottom: 0.4rem;
    list-style: none;
}

/* ===== LINKS ===== */
.link {
    color: white;
    text-decoration-color: rgba(255, 255, 255, 0.7); /* ðŸ‘ˆ visible underline */
    text-underline-offset: 3px;
}

.link:hover {
    text-decoration: underline;
    color: yellow;
}
/* ===== RIGHT (IMAGE) ===== */
.peopleRight {
    flex: 0 0 30%;            /* exactly 50% width */
    display: flex;
    justify-content: center;   /* horizontal centering of image */
    align-items: center;       /* vertical centering of image */
}


.peopleImg {
    height: 60vh;
    max-width: 55rem;
    margin: 0 auto; /* center horizontally */
    object-fit: cover;
    border-radius: 2rem;
}

/* ===== MOBILE ===== */
@media screen and (max-width: 840px) {
    #people {
        flex-direction: column;
    }

    .peopleRight {
        margin-top: 2rem;
    }

    .peopleImg {
        height: 35vh;
    }

    .peopleTitle {
        font-size: 2.4rem;
    }
}
